.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
}

.nav-menu{
    display: flex;
}

.nav-menu li {
    padding: 0 1 rem;
}

.nav-menu li a {
    font-size: 1.2rem;
    font-weight: 500;
}

.hamburger {
    display: none;
}

@media screen and (max-width:1040px){
    .nav-menu {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height:100vh;
        background: rgba (0, 0, 0, 0.9);
        position: abosolute;
        top: 0;
        left: 0;
        z-index: -3;
    }


.nav-menu li {
    padding: 1rem 0;
}

.nav-menu li a{
    font-size: 2rem;

}

.hamburger {
    display: initial;
}

}
.AccordionButton {
    border: 1px solid #ccc;
  }

  .AccordionButton#Nav1:hover {
    background-color: #333;
    color: #fff;
    border: 1px solid #333;
  }